import React from 'react';
import styled from 'styled-components';
import { Link, graphql } from 'gatsby';
import Hero from '../components/shared/hero';
import Layout from "../components/layout"
import '../styles/promo.css'
import SEO from '../components/seo';
import meta from "../constants/meta-descriptions";

const PromoPage = ({ data }) => {

  const homePromotions = sliceArray(data.homePromotions.edges);

  return (
    <>
      <Layout
        activeTab={'/promocii'}>
        <SEO
          title={meta.promo.title}
          description={meta.promo.metaDescription}
        />
        <Hero
          heroImage={data.heroImage.childImageSharp.fluid.src}
          imageClass='promo-all-hero'
          title={(<h1 className='text-white mt-3'>Нашите пакети – по-добри<br />от тези под коледната елха</h1>)}
        />

        <div className='container text-center my-5'>
          <div className='mt-5'>
            {homePromotions.map((arr, index) => (
              <PromoDeck
                key={index}
                arr={arr}
                order={index % 2 !== 0}
              />
            ))}
          </div>

        </div>
      </Layout>
    </>
  )
}

const PromoDeck = ({ arr, order }) => {
  return (
    <>
      <div className='row d-flex p-0 m-0'>
        <div className={'col-12 col-xl-4 p-0 m-0 ' + (order ? 'order-0' : 'order-1')}>
          <div className='row p-0 m-0'>
            <div className='col-12 col-xl-12 p-0 m-0'>
              {(arr[2] != null) && <PromoCard {...arr[2].node.frontmatter} slug={arr[2].node.fields.slug} isBig={false} />}
            </div>
            <div className='col-12 col-xl-12 p-0 m-0'>
              {(arr[1] != null) && <PromoCard {...arr[1].node.frontmatter} slug={arr[1].node.fields.slug} isBig={false} />}
            </div>
          </div>
        </div>
        <div className={'col-12 col-xl-8 p-0 m-0 order-0' + (order ? 'order-1' : 'order-0')}>
          {(arr[0] != null) && <PromoCard {...arr[0].node.frontmatter} slug={arr[0].node.fields.slug} isBig={true} />}
        </div>
      </div>
    </>
  )
}

export const PromoCard = ({ title, subtitle, image, slug, isBig }) => {
  const CardWrapper = styled.div`
    background: #FFFFFF;
    box-shadow: 0px 0px 30px rgba(51, 1, 109, 0.1);
    border-radius: 8px;
    width: 100%;
    padding: 0;
    min-height: 15rem;

    @media screen and (min-width: 1200px) {
      height: ${isBig ? '34.5rem' : '17rem'};
    }
 `;

  const SquareImg = styled.div`
    background-image: url(${props => props.bgImage});
    width: 100%;
    padding-bottom: 100%;
    background-size: cover;
    background-position: center;
  `;

  return (
    <>
      <div className='p-1'>
        <CardWrapper isBig={isBig}>
          <Link to={slug}>
            <img src={image.childImageSharp.fixed.src} className='img-thumbnail promo-card' alt={title} />
            <div className='text-left p-2'>
              <h3>{title}</h3>
              <p>{subtitle}</p>
            </div>
          </Link>
        </CardWrapper>
      </div>
    </>
  )
}

const sliceArray = (arr) => {
  let newArray = [];
  let i = 0;
  let newArrayIndex = 0;

  while (i < arr.length) {
    if (i + 1 > (arr.length - 1) || i + 2 > (arr.length - 1) || i + 3 > (arr.length - 1)) {
      newArray[newArrayIndex] = arr.slice(i, arr.length);
      break;
    } else {
      newArray[newArrayIndex++] = arr.slice(i, i + 3);
      i += 3;
    }
  }

  return newArray;
}

export const query = graphql`
  query PromoQuery {
    homePromotions: allMarkdownRemark(filter: {frontmatter: {tag: {eq: "za-doma"}}}) {
      edges {
        node {
          id
          fields {
            slug
          }
          frontmatter {
            title
            subtitle
            image {
              childImageSharp {
                fixed {
                  src
                }
              }
            }
          }
        }
      }
    }
    heroImage: file(relativePath: {eq: "promo/promo-header.png"}) {
      childImageSharp {
        fluid(maxHeight: 800) {
          src
        }
      }
    }
  }
`;

export default PromoPage;